import { observable, runInAction, action } from 'mobx'
import auth from '../utils/auth'

export default new class User {
  @observable
  id = undefined

  @observable
  displayName = undefined

  @observable
  image = undefined

  @observable
  options = {
    preference: undefined
  }

  @observable
  aliyun = {}

  @observable
  tecent = {}

  @observable
  qiniu = {}

  @observable
  upyun = {}

  constructor () {
    this.requestUserInfo()
  }

  @action
  requestUserInfo = () => {
    auth().then(user => {
      if (!user.id) return
      runInAction(() => {
        Object.assign(this, user)
      })
    })
  }

  @action
  changeOptionsPreference = preference => {
    this.options.preference = preference
  }
}



