import storage from '../storage/pictures'
import isType from 'sewing/libs/isType'
import notice from '../utils/notice'
import user from '../stores/user'
import prefetch from './prefetch'
import { t } from 'i18next'
import token from './token'
import axios from 'axios'
import copy from './copy'

export function smms (file, config) {
  const formData = new FormData()
  formData.append('smfile', file)
  return axios.post(process.env.SMMS, formData, config)
}

export function avi (file, config) {
  const formData = new FormData()
  formData.append(typeof file === 'string' ? 'url' : 'file', file)
  formData.append('type', user.options.preference)
  return axios.post(process.env.API + '/upload', formData, config)
}

function error (err) {
  if (!axios.isCancel(err)) {
    notice({ title: t('upload.fail') }, 'error')
    return Promise.reject(err)
  }
}

export default (file, baseConfig) => {
  const request = !user.id || !isType(file, 'String')
    ? smms(file, baseConfig)
    : avi(file, { ...baseConfig, withCredentials: true })

  return request
    .then(({ data: { data: { url } } }) => {
      if (!url) return Promise.reject(new Error('Upload Failure'))
      if (user.options.autoCopyURL) copy(url)
      notice({ title: t('upload.success') }, 'success')
      storage.add({ url, token: token.token })
      prefetch(url)
      return { url }
    }, error).then(null, error)
}



