import { register } from 'register-service-worker'
import { render } from 'react-dom'
import { isProd } from './utils/'
import { message } from 'antd'
import App from './pages/App'
import React from 'react'
import './utils/i18next'
import 'normalize.css'
import './stores/user'

const app = document.getElementById('app')

if (isProd && navigator.serviceWorker) {
  register('/service-worker.js', {
    ready () {
      console.log('[avi:pwa] Service worker is active.')
    },
    cached () {
      console.log('[avi:pwa] Content has been cached for offline use.')
    },
    updated () {
      console.log('[avi:pwa] Content updated.')
    },
    offline () {
      console.log('[avi:pwa] No internet connection found. App is running in offline mode.')
    },
    error (err) {
      console.error('[avi:pwa] Error during service worker registration:', err)
    }
  })
}

message.config({
  getContainer: () => app,
  top: 40
})

render(<App />, app)



