import { message } from 'antd'
import { t } from 'i18next'
import api from './api'

export default settings => {
  return api.post('/setting', { settings })
    .then(
      () => { message.success(t('setting.save.success')) },
      () => { message.success(t('setting.save.fail')) }
    )
}



